      <!-- ÊîØ‰ªòÁä∂ÊÄÅÂç°Áâá -->
      <view class="status-card">
        <view
          class="status-icon"
          :class="getStatusClass(orderDetail.payStatus)"
        >
          <text v-if="orderDetail.payStatus === 2">‚úì</text>
          <text v-else-if="orderDetail.payStatus === 1">‚è≥</text>
          <text v-else-if="orderDetail.payStatus === 3">üîÑ</text>
          <text v-else-if="orderDetail.payStatus === 4">‚Ü©</text>
          <text v-else>‚úó</text>
        </view>
        <text class="status-text">{{
          getStatusText(orderDetail.payStatus)
        }}</text>
        <text class="amount">¬•{{ orderDetail.payPrice }}</text>
      </view>

      <!-- Êäï‰øù‰∫∫‰ø°ÊÅØ -->
      <view class="info-section">
        <view class="section-title">Êäï‰øù‰∫∫‰ø°ÊÅØ</view>
        <view class="info-list">
          <view class="info-item">
            <text class="label">ÂßìÂêç</text>
            <text class="value">{{ orderDetail.tbName || "-" }}</text>
          </view>
          <view class="info-item">
            <text class="label">Ë∫´‰ªΩËØÅÂè∑</text>
            <text class="value">{{ formatIdCard(orderDetail.tbIdCard) }}</text>
          </view>
          <view class="info-item">
            <text class="label">ÊâãÊú∫Âè∑</text>
            <text class="value">{{ orderDetail.tbPhone || "-" }}</text>
          </view>
          <view class="info-item">
            <text class="label">‰ΩèÂùÄ</text>
            <text class="value">{{ orderDetail.tbAddress || "-" }}</text>
          </view>
        </view>
      </view>

      <!-- Ë¢´‰øù‰∫∫‰ø°ÊÅØ -->
      <view class="info-section">
        <view class="section-title">Ë¢´‰øù‰∫∫‰ø°ÊÅØ</view>
        <view class="info-list">
          <view class="info-item">
            <text class="label">ÂßìÂêç</text>
            <text class="value">{{ orderDetail.bbName || "-" }}</text>
          </view>
          <view class="info-item">
            <text class="label">Ë∫´‰ªΩËØÅÂè∑</text>
            <text class="value">{{ formatIdCard(orderDetail.bbIdCard) }}</text>
          </view>
          <view class="info-item">
            <text class="label">ÊâãÊú∫Âè∑</text>
            <text class="value">{{ orderDetail.bbPhone || "-" }}</text>
          </view>
          <view class="info-item">
            <text class="label">‰ΩèÂùÄ</text>
            <text class="value">{{ orderDetail.bbAddress || "-" }}</text>
          </view>
          <view class="info-item">
            <text class="label">Â≠¶Ê†°</text>
            <text class="value">{{ orderDetail.bbSchool || "-" }}</text>
          </view>
          <view class="info-item">
            <text class="label">Áè≠Á∫ß</text>
            <text class="value">{{ orderDetail.bbClass || "-" }}</text>
          </view>
        </view>
      </view>

      <!-- ÊîØ‰ªò‰ø°ÊÅØ -->
      <view class="info-section">
        <view class="section-title">ÊîØ‰ªò‰ø°ÊÅØ</view>
        <view class="info-list">
          <view class="info-item">
            <text class="label">ÊîØ‰ªòÈáëÈ¢ù</text>
            <text class="value amount-text">¬•{{ orderDetail.payPrice }}</text>
          </view>
          <view class="info-item">
            <text class="label">ÊîØ‰ªòÊó∂Èó¥</text>
            <text class="value">{{ formatDate(orderDetail.payTime) }}</text>
          </view>
          <view class="info-item">
            <text class="label">ÊîØ‰ªòÁä∂ÊÄÅ</text>
            <text
              class="value"
              :class="getStatusClass(orderDetail.payStatus)"
              >{{ getStatusText(orderDetail.payStatus) }}</text
            >
          </view>
        </view>
      </view>

      <!-- Êìç‰ΩúÊåâÈíÆ - Âè™ÊúâÂæÖÊîØ‰ªòÊó∂ÊòæÁ§∫ -->
      <view class="action-section" v-if="orderDetail.payStatus === 1">
        <button class="pay-btn" @click="goToPay">ÊîØ‰ªòËÆ¢Âçï</button>
        <button class="delete-btn" @click="deleteOrder">Âà†Èô§ËÆ¢Âçï</button>
      </view>
    </view>

    <!-- ÈîôËØØÁä∂ÊÄÅ -->
    <view v-else class="error-state">
      <text>ËÆ¢Âçï‰ø°ÊÅØÂä†ËΩΩÂ§±Ë¥•</text>
    </view>
  </view>
