<script>
import { formatAmount } from "@/utils/payment.js";
import { businessApi } from "@/utils/api.js";

export default {
  name: "PaymentDetail",
  data() {
    return {
      orderId: null,
      orderDetail: null,
      loading: true,
    };
  },
  methods: {
    formatAmount,

    goBack() {
      uni.navigateBack();
    },

    getStatusText(status) {
      const statusMap = {
        1: "ÂæÖÊîØ‰ªò",
        2: "Â∑≤ÊîØ‰ªò",
        3: "Áî≥ËØ∑ÈÄÄÊ¨æ",
        4: "Â∑≤ÈÄÄÊ¨æ",
      };
      return statusMap[status] || "Êú™Áü•Áä∂ÊÄÅ";
    },

    getStatusClass(status) {
      const classMap = {
        1: "unpaid",
        2: "paid",
        3: "refunding",
        4: "refunded",
      };
      return classMap[status] || "unknown";
    },

    formatDate(dateString) {
      if (!dateString) return "ÂæÖÊîØ‰ªò";
      const date = new Date(dateString);
      return date.toLocaleString("zh-CN", {
        year: "numeric",
        month: "2-digit",
        day: "2-digit",
        hour: "2-digit",
        minute: "2-digit",
        second: "2-digit",
      });
    },

    formatIdCard(idCard) {
      if (!idCard) return "-";
      // Ë∫´‰ªΩËØÅÂè∑ËÑ±ÊïèÂ§ÑÁêÜ
      if (idCard.length >= 10) {
        return (
          idCard.substring(0, 6) + "****" + idCard.substring(idCard.length - 4)
        );
      }
      return idCard;
    },

    goToPay() {
      // ÊûÑÂª∫ÊîØ‰ªòÈ°µÈù¢ÂèÇÊï∞
      const paymentParams = {
        id: this.orderId,
      };

      // Ë∑≥ËΩ¨Âà∞ÊîØ‰ªòÈ°µÈù¢
      const queryString = Object.keys(paymentParams)
        .map((key) => `${key}=${encodeURIComponent(paymentParams[key])}`)
        .join("&");
      uni.redirectTo({
        url: `/pages/payment/payment?${queryString}`,
      });
    },

    async deleteOrder() {
      try {
        const result = await uni.showModal({
          title: "Á°ÆËÆ§Âà†Èô§",
          content: "Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™ËÆ¢ÂçïÂêóÔºü",
          confirmText: "Âà†Èô§",
          cancelText: "ÂèñÊ∂à",
        });

        if (result.confirm) {
          uni.showLoading({ title: "Âà†Èô§‰∏≠..." });

          await businessApi.deleteOrder(this.orderId);

          uni.hideLoading();
          uni.showToast({
            title: "Âà†Èô§ÊàêÂäü",
            icon: "success",
          });

          // Âª∂ËøüËøîÂõû‰∏ä‰∏ÄÈ°µ
          setTimeout(() => {
            uni.navigateBack();
          }, 1500);
        }
      } catch (error) {
        console.error("Âà†Èô§ËÆ¢ÂçïÂ§±Ë¥•:", error);
        uni.hideLoading();
        uni.showToast({
          title: "Âà†Èô§Â§±Ë¥•ÔºåËØ∑ÈáçËØï",
          icon: "none",
        });
      }
    },

    async loadOrderDetail() {
      if (!this.orderId) {
        uni.showToast({
          title: "ËÆ¢ÂçïID‰∏çËÉΩ‰∏∫Á©∫",
          icon: "none",
        });
        return;
      }

      try {
        this.loading = true;
        const response = await businessApi.getOrderDetail({ id: this.orderId });

        if (response && response.data) {
          this.orderDetail = response.data;
        } else {
          throw new Error("Ëé∑ÂèñËÆ¢ÂçïËØ¶ÊÉÖÂ§±Ë¥•");
        }
      } catch (error) {
        console.error("Âä†ËΩΩËÆ¢ÂçïËØ¶ÊÉÖÂ§±Ë¥•:", error);
        uni.showToast({
          title: "Âä†ËΩΩÂ§±Ë¥•ÔºåËØ∑ÈáçËØï",
          icon: "none",
        });
      } finally {
        this.loading = false;
      }
    },
  },

  onLoad(options) {
    // Êé•Êî∂È°µÈù¢ÂèÇÊï∞
    if (options.id) {
      this.orderId = parseInt(options.id);
      // Âä†ËΩΩËÆ¢ÂçïËØ¶ÊÉÖ
      this.loadOrderDetail();
    } else {
      uni.showToast({
        title: "ËÆ¢ÂçïID‰∏çËÉΩ‰∏∫Á©∫",
        icon: "none",
      });
      this.loading = false;
    }
  },

  // È°µÈù¢ÊòæÁ§∫Êó∂Âà∑Êñ∞Êï∞ÊçÆ
  onShow() {
    if (this.orderId) {
      this.loadOrderDetail();
    }
  },
};
</script>

